"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Trash2, Edit2, Plus } from "lucide-react"

interface Folder {
  id: string
  name: string
  createdAt: Date
}

export default function FolderManagement() {
  const [folders, setFolders] = useState<Folder[]>([])
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [newFolderName, setNewFolderName] = useState("")
  const [editingId, setEditingId] = useState<string | null>(null)
  const [editingName, setEditingName] = useState("")

  // Load folders from localStorage
  useEffect(() => {
    const savedFolders = localStorage.getItem("folders")
    if (savedFolders) {
      const parsed = JSON.parse(savedFolders)
      setFolders(
        parsed.map((f: any) => ({
          ...f,
          createdAt: new Date(f.createdAt),
        })),
      )
    }
  }, [])

  // Save folders to localStorage
  useEffect(() => {
    localStorage.setItem("folders", JSON.stringify(folders))
  }, [folders])

  const handleCreateFolder = (e: React.FormEvent) => {
    e.preventDefault()
    if (!newFolderName.trim()) return

    const newFolder: Folder = {
      id: Date.now().toString(),
      name: newFolderName,
      createdAt: new Date(),
    }

    setFolders([...folders, newFolder])
    setNewFolderName("")
    setIsModalOpen(false)
  }

  const handleRenameFolder = (id: string) => {
    if (!editingName.trim()) {
      setEditingId(null)
      return
    }

    setFolders(folders.map((f) => (f.id === id ? { ...f, name: editingName } : f)))
    setEditingId(null)
    setEditingName("")
  }

  const handleDeleteFolder = (id: string) => {
    if (confirm("Are you sure you want to delete this folder?")) {
      setFolders(folders.filter((f) => f.id !== id))
    }
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-foreground">My Workspace</h1>
          <p className="text-muted-foreground mt-1">Create and organize your folders</p>
        </div>
        <Button
          onClick={() => setIsModalOpen(true)}
          className="bg-primary hover:bg-primary/90 text-primary-foreground flex items-center gap-2"
        >
          <Plus className="w-4 h-4" />
          New Folder
        </Button>
      </div>

      {/* Create Folder Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <Card className="w-full max-w-sm">
            <div className="p-6">
              <h2 className="text-xl font-bold mb-4 text-foreground">Create New Folder</h2>
              <form onSubmit={handleCreateFolder} className="space-y-4">
                <div>
                  <label className="text-sm font-medium text-foreground">Folder Name</label>
                  <Input
                    type="text"
                    placeholder="Enter folder name..."
                    value={newFolderName}
                    onChange={(e) => setNewFolderName(e.target.value)}
                    autoFocus
                    className="mt-2"
                  />
                </div>
                <div className="flex gap-2">
                  <Button type="submit" className="flex-1 bg-primary hover:bg-primary/90 text-primary-foreground">
                    Create
                  </Button>
                  <Button
                    type="button"
                    variant="outline"
                    className="flex-1 bg-transparent"
                    onClick={() => {
                      setIsModalOpen(false)
                      setNewFolderName("")
                    }}
                  >
                    Cancel
                  </Button>
                </div>
              </form>
            </div>
          </Card>
        </div>
      )}

      {/* Folders Grid */}
      {folders.length === 0 ? (
        <Card className="p-12 text-center border-dashed">
          <p className="text-muted-foreground">No folders yet. Create one to get started!</p>
        </Card>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {folders.map((folder) => (
            <Card key={folder.id} className="p-4 hover:shadow-md transition-shadow">
              <div className="space-y-3">
                {editingId === folder.id ? (
                  <Input
                    type="text"
                    value={editingName}
                    onChange={(e) => setEditingName(e.target.value)}
                    autoFocus
                    className="font-semibold"
                  />
                ) : (
                  <h3 className="font-semibold text-foreground text-lg truncate">{folder.name}</h3>
                )}
                <p className="text-xs text-muted-foreground">Created {folder.createdAt.toLocaleDateString()}</p>

                <div className="flex gap-2 pt-2">
                  {editingId === folder.id ? (
                    <>
                      <Button
                        size="sm"
                        onClick={() => handleRenameFolder(folder.id)}
                        className="flex-1 bg-primary text-primary-foreground hover:bg-primary/90"
                      >
                        Save
                      </Button>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => {
                          setEditingId(null)
                          setEditingName("")
                        }}
                        className="flex-1"
                      >
                        Cancel
                      </Button>
                    </>
                  ) : (
                    <>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => {
                          setEditingId(folder.id)
                          setEditingName(folder.name)
                        }}
                        className="flex-1 flex items-center justify-center gap-2"
                      >
                        <Edit2 className="w-3 h-3" />
                        Rename
                      </Button>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => handleDeleteFolder(folder.id)}
                        className="flex-1 flex items-center justify-center gap-2 text-destructive hover:text-destructive"
                      >
                        <Trash2 className="w-3 h-3" />
                        Delete
                      </Button>
                    </>
                  )}
                </div>
              </div>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
